"use client"
import React, { useState, useEffect } from 'react';
import FlightCard from '../Card/FlightCard';
import { collection, getDocs } from 'firebase/firestore';
import { db } from '../../../lib/firebase';

const FeaturedFlight = () => {
    const [isActive, setIsActive] = useState('ALL');
    const [flights, setFlights] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchFlights = async () => {
            try {
                setLoading(true);
                const flightsCollection = collection(db, 'deals');
                const flightsSnapshot = await getDocs(flightsCollection);
                
                const flightsData = flightsSnapshot.docs.map(doc => ({
                    id: doc.id,
                    ...doc.data()
                }));
                
                setFlights(flightsData);
            } catch (error) {
                console.error("Error fetching flights:", error);
                // If there's an error, we'll just display an empty array
                setFlights([]);
            } finally {
                setLoading(false);
            }
        };

        fetchFlights();
    }, []);

    // Filter flights based on the active tab
    const filteredFlights = flights.filter(flight => {
        if (isActive === 'ALL') return true;
        if (isActive === 'OneWay') return flight.type === 'One Way Flight';
        if (isActive === 'RoundTrip') return flight.type === 'Round Trip Flight';
        return true;
    });

    return (
        <section className="featured-flight-section section-padding fix">
            <div className="container">
                <div className="section-title-area">
                    <div className="section-title">
                        <span className="sub-title wow fadeInUp">
                            Airlines Flight
                        </span>
                        <h2 className="wow fadeInUp wow" data-wow-delay=".2s">
                            Featured Umrah Flight Deals
                        </h2>
                    </div>
                    <ul className="nav">
                        <li className={`nav-item wow fadeInUp ${isActive === 'ALL' ? 'active' : ''}`} onClick={() => setIsActive('ALL')} data-wow-delay=".3s">
                            <a href="#all" data-bs-toggle="tab" className="nav-link active">
                                ALL
                            </a>
                        </li>
                        <li className={`nav-item wow fadeInUp ${isActive === 'OneWay' ? 'active' : ''}`} onClick={() => setIsActive('OneWay')} data-wow-delay=".5s">
                            <a href="#oneway" data-bs-toggle="tab" className="nav-link">
                                ONE WAY
                            </a>
                        </li>
                        <li className={`nav-item wow fadeInUp ${isActive === 'RoundTrip' ? 'active' : ''}`} onClick={() => setIsActive('RoundTrip')} data-wow-delay=".7s">
                            <a href="#roundtrip" data-bs-toggle="tab" className="nav-link">
                                ROUND TRIP
                            </a>
                        </li>
                    </ul>
                </div>

                <div className="tab-content">
                    <div className={`tab-pane ${isActive === 'ALL' || isActive === 'OneWay' || isActive === 'RoundTrip' ? 'active' : ''}`}>
                        {loading ? (
                            <div className="text-center py-5">
                                <div className="spinner-border text-primary" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                </div>
                                <p className="mt-3">Loading flights...</p>
                            </div>
                        ) : filteredFlights.length > 0 ? (
                            <div className="row">
                                {filteredFlights.map((flight) => (
                                    <div key={flight.id} className="col-xl-4 col-lg-6 col-md-6 wow fadeInUp" data-wow-delay=".3s">
                                        <FlightCard
                                            img={flight.img}
                                            name={flight.name}
                                            oneway={flight.type || (flight.flytype === 'Direct Flight' ? 'One Way Flight' : 'Round Trip Flight')}
                                            destination={`${flight.location1} → ${flight.location2}`}
                                            price={flight.price}
                                            review={flight.review || `⭐ ${Math.floor(Math.random() * 100) + 400}+ Reviews`}
                                        />
                                    </div>
                                ))}
                            </div>
                        ) : (
                            <div className="text-center py-5">
                                <p>No flights found. Please check back later.</p>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </section>
    );
};

export default FeaturedFlight;
                                