import Image from 'next/image';
import React from 'react';

const TourCard = () => {
    return (
        <div className="tour-wrapper tour-field-area " style={{marginRight: '30px'}}>
            {/* Departure City */}
            <div className="tour-item style-5 " >
                <div className="icon">
                    <Image src="/assets/img/icon/40.svg" alt="departure" width={44} height={30} />
                </div>
                <div className="content">
                    <h5>Departure City</h5>
                    <div className="form">
                        <select className="single-select ">
                            <option>London - UK</option>
                            <option>Manchester - UK</option>
                            <option>Birmingham - UK</option>
                            <option>Glasgow - UK</option>
                            <option>Leeds - UK</option>
                        </select>
                    </div>
                </div>
            </div>

            {/* Arrival City */}
            <div className="tour-item">
                <div className="icon">
                    <Image src="/assets/img/icon/41.svg" alt="arrival" width={39} height={30} />
                </div>
                <div className="content">
                    <h5>Arrival City</h5>
                    <div className="form">
                        <select className="single-select w-100">
                            <option>Jeddah - Saudi Arabia</option>
                            <option>Medina - Saudi Arabia</option>
                        </select>
                    </div>
                </div>
            </div>

            {/* Dates */}
            <div className="tour-item">
                <div className="icon">
                    <Image src="/assets/img/icon/42.svg" alt="dates" width={30} height={30} />
                </div>
                <div className="content">
                    <h5>Check In / Check Out</h5>
                    <div className="form-clt">
                        <input type="date" id="date1" name="date1" />
                    </div>
                </div>
            </div>

            {/* Passengers */}
            <div className="tour-item">
                <div className="icon">
                    <Image src="/assets/img/icon/43.svg" alt="passenger" width={37} height={30} />
                </div>
                <div className="content">
                    <h5>Passengers</h5>
                    <div className="form">
                        <select className="single-select w-100">
                            <option>01 Adult</option>
                            <option>02 Adults</option>
                            <option>03 Adults</option>
                            <option>04 Adults</option>
                            <option>05 Adults</option>
                        </select>
                    </div>
                </div>
                <button type="submit" className="theme-btn">
                    Search <i className="bi bi-search"></i>
                </button>
            </div>
        </div>
    );
};

export default TourCard;
